import course_data_bbwl01_01 from '../data/BBWL01-01.js';
import video_data_bbwl01_01 from '../data/BBWL01-01_videos.js';
import course_data_bbwl02_01 from '../data/BBWL02-01.js';
import video_data_bbwl02_01 from '../data/BBWL02-01_videos.js';
import course_data_dlbsaesa01 from '../data/DLBSAESA01.js';
import video_data_dlbsaesa01 from '../data/DLBSAESA01_videos.js';

import { NODE_TYPES, AVAILABLE_COURSES } from './consts'

type Video = {
    kalturaId: string
    dataUrl: string
    thumbnailUrl: string
    name: string
    contentId: string
}

const prepare = (course_data, video_data) => {
    // add videos to the graph
    Object.entries(video_data).forEach(entry => {
        /* @ts-ignore */
        const [parent, video_entries]: [string, Video[]] = entry
        video_entries.forEach(video => {
            if (!course_data[video.contentId]) {
                course_data[video.contentId] = {
                    title: video.name,
                    type: NODE_TYPES.VIDEO,
                    link: video.dataUrl,
                    parents: [parent]
                }
            } else {
                course_data[video.contentId].parents.push(parent)
            }
        })
    })

    return course_data
}

const preparedData = [
    [AVAILABLE_COURSES["BBWL01-01"], course_data_bbwl01_01, video_data_bbwl01_01],
    [AVAILABLE_COURSES["BBWL02-01"], course_data_bbwl02_01, video_data_bbwl02_01],
    [AVAILABLE_COURSES["DLBSAESA01"], course_data_dlbsaesa01, video_data_dlbsaesa01]
].reduce(
    (acc, [course_code, course_data, video_data]) => {
        acc[course_code] = prepare(course_data, video_data)
        return acc
    } , {}
)

export default preparedData
