import preparedData from './preparedData'
import GraphRenderer from './GraphRenderer'
import { NODE_COLOR, NODE_STATE, NODE_ELEMENT } from './style'

import { DEFAULT_NODE_TYPE, DEFAULT_HIGHLIGHT_LEVEL, AVAILABLE_RENDER_TYPES } from './consts'

const main = () => {
    const urlSearchParams = new URLSearchParams(window.location.search)
    const params = Object.fromEntries(urlSearchParams.entries());

    if (!params["course_code"] || params["course_code"].length <= 1) {
        alert("No course specified")
    }

    const courseCode = params["course_code"]
    const highlightChildrenLevel = params["c"] ? parseInt(params["c"]) : DEFAULT_HIGHLIGHT_LEVEL
    const renderTypes = AVAILABLE_RENDER_TYPES

    const container = document.getElementById("container");
    const graphRenderer = new GraphRenderer(container)

    graphRenderer.update(courseCode, preparedData[courseCode], highlightChildrenLevel, renderTypes)


    document.querySelector("#legend").innerHTML = `
        <div class="entry"><strong>Legend:</strong></div>
        ${AVAILABLE_RENDER_TYPES.map(type => `
            <div
                class="entry type"
                data-type="${type}"
            >
                <div
                    class="circle"
                    style="background-color: ${NODE_COLOR[type][NODE_STATE.ACTIVE][NODE_ELEMENT.NODE]}
                "></div>
                ${type}
            </div>
        `).join('')}
    `

    const updateLegend = (activeRenderTypes) => {
        document.querySelectorAll('#legend .type').forEach(element => {
            const type = element.getAttribute('data-type')
            /* @ts-ignore */
            element.querySelector(".circle").style.backgroundColor = activeRenderTypes.includes(type)
                ? NODE_COLOR[type][NODE_STATE.ACTIVE][NODE_ELEMENT.NODE]
                : NODE_COLOR[DEFAULT_NODE_TYPE][NODE_STATE.DEFAULT][NODE_ELEMENT.NODE]
        })

    }
    updateLegend(renderTypes)



    document.querySelectorAll("#legend .type").forEach(element => {
        element.addEventListener("click", e => {
            const target = e.target as HTMLElement;
            const selectedType = target.getAttribute("data-type")
            const updatedRenderTypes: string[] = []
            for (let index in AVAILABLE_RENDER_TYPES) {
                updatedRenderTypes.push(AVAILABLE_RENDER_TYPES[index])
                if (AVAILABLE_RENDER_TYPES[index] == selectedType) {
                    break;
                }
            }
            graphRenderer.update(courseCode, preparedData[courseCode], highlightChildrenLevel, updatedRenderTypes)
            updateLegend(updatedRenderTypes)
        })
    })
}

/* @ts-ignore */
document.onload = main()
